import React from "react"
import styled from "styled-components"
import * as FillIcons from "../assets/FillIcons"

const FillIcon = ({ name, style, size }) => {
  const FillIcon = FillIcons[name]
  if (FillIcon) {
    const style = { width: size, height: size }
    return (
      <div>
        <FillIcon name={name} />
      </div>
    )
  } else {
    return null
  }
}

FillIcon.displayName = "FillIcon"

FillIcon.defaultProps = {
  size: 16,
  color: "#96AAB4"
}

export default FillIcon



